.search-main{
    max-width: 900px;
}

.vacancy-control-block{
    display: flex;
}

.vacancy-control-block p{
    margin-bottom: 10px;
    margin-left: 20px;
}

.vacancy-control-block__response-block{
    position: relative;
}

.search-main__search-items-list .resume__main-info{
    width: 100%;
}

.response-form-popup{
    position: absolute;
    right: -385px;
    bottom: 30px;

    display: flex;
    flex-direction: column;

    width: 380px;
    height: 320px;
    padding: 10px 30px;

    border: 3px solid #000;
    background-color: #eff5ff;
}

.response-form-popup__textarea{
    min-height: 100px;
    max-height: 200px;
    margin-bottom: 20px;
    padding: 5px;

    resize: vertical;

    background-color: #fff;
}
.response-form-popup input[type='submit']{
    margin-top: 20px;

    background-color: #fff;
}

.response-form-popup .close-popup-btn{
    top: 0;
    right: 5px;
}

.search-side{
    position: sticky;
    flex: 1;
    margin-right: 40px;
    min-width: 280px;
    max-width: 500px;
    min-height: 400px;
    padding: 20px;

    background-color: #fff;
}

.search-side__block{
    margin-bottom: 20px;
}

.search-side__salary-placeholder{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.search-side__grade{
    display: flex;
    flex-direction: column;
}

.checkbox-block > input{
    margin-right: 15px;
}

.search-page{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.search-query-header{
    margin: 30px 0;
}

.search-main__controls-form{
    display: flex;
    justify-content: space-between;

    margin-top: 0;
    margin-bottom: 40px;
    padding: 15px 20px;

    background-color: #fff;
}


.search-input{
    display: flex;
    justify-content: space-between;

    box-sizing: border-box;
    min-width: 270px;
    height: 32px;
    margin-right: 20px;

    border: 2px solid #000;
}

.search-input button{
    height: 32px;
    margin-top: -2px;
    margin-right: -2px;
}

.search-input input{
    margin-left: 10px;
}

.search-main__controls__sort-type-select{
    margin-left: 20px;
}

.search-main__controls__check-container{
    margin-left: 30px;
}

.search-main__controls__check-container label{
    white-space: nowrap;
}

.search-main__controls__sort-type-check{
    height: 20px !important;
}


@media (max-width : 768px){
    .search-page{
        flex-direction: column;
    }

    .search-side{
        width: 100%;
        padding: 20px 10px;

        align-self: center;
        margin-right: 0;

    }

    .search-main__controls-form{
        margin: 20px auto;
        max-width: 500px;
    }
}

